import Photos from "./photos"
import GridListTile from "@material-ui/core/GridListTile"
import GridListTileBar from "@material-ui/core/GridListTileBar"
import IconButton from "@material-ui/core/IconButton"
import FullscreenIcon from "@material-ui/icons/Fullscreen"
import GridList from "@material-ui/core/GridList"
import React from "react"

class Album extends React.Component{
  constructor() {
    super();
    this.state = {
      width: 0, // CAUSING PORT PROBLEMS
    };
  }
  // event listener for window size (to determine whether user is in mobile or not).
  componentDidMount() {
    this.handleWindowSizeChange()
    window.addEventListener('resize', this.handleWindowSizeChange);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }
  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };
  render() {
    const {width} = this.state;
    const isMobile = width <= 800;
    if (isMobile) {
      return (
        <GridList cellHeight={"150"} cols={3}>
          {Photos.map((tile) => (
            <GridListTile key={tile.img} cols={tile.cols || 1} >
              <img src={tile.img} alt={tile.title} />
              <GridListTileBar
                title={tile.title}
                subtitle={tile.date}
                actionIcon={
                  <IconButton>
                    <a href={tile.link} target="_blank" rel="noreferrer">
                      <FullscreenIcon>
                        Fullscreen
                      </FullscreenIcon>
                    </a>
                  </IconButton>
                }
              />
            </GridListTile>
          ))}
        </GridList>
      );
    } else {
      return (
        <div>
          <div style={{objectFit: 'contain', marginLeft: '12vw', marginRight:'12vw', marginTop:'5vh', marginBottom:'5vh'}} >
            <GridList cellHeight={'300'} cols={3}>
              {Photos.map((tile) => (
                <GridListTile key={tile.img} cols={tile.cols || 1} >
                  <img src={tile.img} alt={tile.title} />
                  <GridListTileBar
                    title={tile.title}
                    subtitle={tile.date}
                    actionIcon={
                      <IconButton>
                        <a href={tile.link} target="_blank" rel="noreferrer">
                          <FullscreenIcon>
                            Fullscreen
                          </FullscreenIcon>
                        </a>
                      </IconButton>
                    }
                  />
                </GridListTile>
              ))}
            </GridList>
          </div>
        </div>
      );
    }
  }
}

export default Album

